import qs from "qs"
import { FileCollection } from "@rp/common/utils"
import { axiosClient } from "@rp/react/axios-client"
import { FILES_BASE_URL, FILES_ROOT } from "../file.constants"

const getAllProcessingFiles = async () => {
  const query = qs.stringify({
    page_size: 5000,
    status: "PROCESSING",
  })
  const url = `${FILES_BASE_URL}${FILES_ROOT}/?${query}`
  const { data } = await axiosClient.get<FileCollection>(url)
  return data
}

export { getAllProcessingFiles }
